import React, { Component } from 'react';
import { Col, Row, Image } from 'react-bootstrap';
class Header extends Component {
  render() {
    return (
      <Row className="header">
        <Col md={4}>
          <h1>John Michael Ferraris</h1>
        </Col>
        <Col md={1} mdOffset={6}>
          <a href="https://github.com/jhnferraris" target="_blank" rel="noopener noreferrer">
            <Image className="github-link" src={require('../styles/images/privateinvestocat.ico')} alt="github link" responsive rounded />
          </a>
        </Col>
        <Col md={1}>
          <a href="https://www.linkedin.com/in/johnferraris/" target="_blank" rel="noopener noreferrer">
            <Image className="linkedin-link" src={require('../styles/images/linkedin.png')} alt="linkedin link" responsive rounded />
          </a>
        </Col>
      </Row>
    );
  }
}

export default Header;



